var myApp = angular.module('app');
myApp.directive('customTable', function () {
  return {
    restrict: 'EA', //E = element, A = attribute, C = class, M = comment
    templateUrl: 'app/dataTable2/dataTable.html',
    controller: tableControllerFunction //Embed a custom controller in the directive
  };
});

myApp.service('filterDataService', function () {


  this.searched = function (valLists, toSearch) {
    return _.filter(valLists,
      function (i) {
        /* Search Text in all 3 fields */
        return searchUtil(i, toSearch);
      });
  };

  this.paged = function (valLists, pageSize) {
    var retVal = [];
    for (var i = 0; i < valLists.length; i++) {
      if (i % pageSize === 0) {
        retVal[Math.floor(i / pageSize)] = [valLists[i]];
      } else {
        retVal[Math.floor(i / pageSize)].push(valLists[i]);
      }
    }
    return retVal;
  };

});

var tableControllerFunction = function ($scope, $filter, filterDataService) {

  $scope.pageSize = 4;
  $scope.allItems = getDummyData();
  $scope.reverse = false;

  $scope.resetAll = function () {
    $scope.filteredList = $scope.allItems;
    $scope.newEmpId = '';
    $scope.newName = '';
    $scope.newEmail = '';
    $scope.searchText = '';
    $scope.currentPage = 0;
    $scope.Header = ['', '', ''];
  };

  $scope.add = function () {
    $scope.allItems.push({
      EmpId: $scope.newEmpId,
      name: $scope.newName,
      Email: $scope.newEmail
    });
    $scope.resetAll();
  }

  $scope.search = function () {
    $scope.filteredList =
      filterDataService.searched($scope.allItems, $scope.searchText);

    if ($scope.searchText === '') {
      $scope.filteredList = $scope.allItems;
    }
    $scope.pagination();
  };


  // Calculate Total Number of Pages based on Search Result
  $scope.pagination = function () {
    $scope.ItemsByPage = filterDataService.paged($scope.filteredList, $scope.pageSize);
  };

  $scope.setPage = function () {
    $scope.currentPage = this.n;
  };

  $scope.firstPage = function () {
    $scope.currentPage = 0;
  };

  $scope.lastPage = function () {
    $scope.currentPage = $scope.ItemsByPage.length - 1;
  };

  $scope.range = function (input, total) {
    var ret = [];
    if (!total) {
      total = input;
      input = 0;
    }
    for (var i = input; i < total; i++) {
      if (i !== 0 && i !== total - 1) {
        ret.push(i);
      }
    }
    return ret;
  };

  $scope.sort = function (sortBy) {
    $scope.resetAll();
    var iconName;

    $scope.columnToOrder = sortBy;

    //$Filter - Standard Service
    $scope.filteredList = $filter('orderBy')($scope.filteredList, $scope.columnToOrder, $scope.reverse);

    if ($scope.reverse) {
      iconName = 'glyphicon glyphicon-chevron-up';
    } else {
      iconName = 'glyphicon glyphicon-chevron-down';
    }

    if (sortBy === 'EmpId') {
      $scope.Header[0] = iconName;
    }
    else if (sortBy === 'name') {
      $scope.Header[1] = iconName;
    } else {
      $scope.Header[2] = iconName;
    }

    $scope.reverse = !$scope.reverse;

    $scope.pagination();
  };

  //By Default sort ny Name
  $scope.sort('name');

};
tableControllerFunction.$inject = ["$scope", "$filter", "filterDataService"];


function searchUtil(item, toSearch) {
  /* Search Text in all 3 fields */
  return (item.name.toLowerCase().indexOf(toSearch.toLowerCase()) > -1 || item.Email.toLowerCase().indexOf(toSearch.toLowerCase()) > -1 || item.EmpId === toSearch) ? true : false;
}

